.navbarItem {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 95%;
  height: 80px;
  padding: 0 30px;
  box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.25);
  border-radius: 13px;
  position: fixed;
  top: 20px;
  left: 50%;
  transform: translate(-50%);
}
.navbar-logo {
  font-size: 2rem;
  font-weight: 600;
  color: #222;
  cursor: pointer;
}
.navbar-menu {
  list-style: none;
  display: grid;
  grid-template-columns: repeat(6, auto);
  grid-gap: 20px;
  font-weight: 500;
  align-items: center;
}

.Nav-links {
  text-decoration: none;
  font-size: 1.2rem;
  font-weight: 600;
  color: #222;
  padding: 0.7rem 1rem;
  white-space: nowrap;
}
.Nav-links i {
  padding-right: 10px;
}
.Nav-links:hover {
  background-color: #01959a;
  color: #fff;
  border-radius: 4px;
  transition: all 0.2s ease-in-out;
}
.Nav-links-Mobile {
  display: none;
}
button {
  padding: 0.5rem 1rem;
  white-space: nowrap;
  border-radius: 0.3rem;
  font-size: 1.2rem;
  border: none;
  font-weight: 600;
}
button:hover {
  background-color: #151516;
  color: #fff;
}
.navbar-icon {
  display: none;
}
@media screen and (max-width: 850px) {
  .navbarItem {
    z-index: 99;
  }

  .navbar-menu {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    width: 100%;
    height: auto;
    position: absolute;
    text-align: center;
    top: 0;
    left: -100%;
    opacity: 1;
    align-items: stretch;
    padding: 80px 0 30px 0;
    margin: 0;
  }
  .navbar-menu.active {
    left: 0;
    opacity: 1;
    z-index: -1;
  }

  .Nav-links {
    display: block;
    width: 100%;
    font-size: 1.2rem;
    padding: 2rem 0;
  }
  .nav-links:hover {
    background-color: #01959a;
    transition: none;
  }
  button {
    display: none;
  }
  .Nav-links-Mobile {
    display: block;
    text-align: center;
    padding: 1.5rem;
    margin: auto;
    border-radius: 4px;
    width: 80%;
    background: #01959a;
    text-decoration: none;
    color: #fff;
    font-weight: 700;
    letter-spacing: 2px;
  }
  .navbar-icon {
    display: block;
  }
  .navbar-icon i {
    font-size: 1.2rem;
    color: #222;
  }
}
